<template>
  <div class="register">
    <!-- <van-cell-group>
      <van-field v-model="user.phone" label="手机号：" placeholder="请输入手机号" />
    </van-cell-group>
    <van-cell-group>
      <van-field v-model="user.nikename" label="昵称：" placeholder="请输入昵称" />
    </van-cell-group>
    <van-cell-group>
      <van-field v-model="user.password" label="密码：" placeholder="请输入密码" />
    </van-cell-group>
    <van-button round type="info" class="btn" @click="register">注册</van-button>-->
    <van-form @submit="onSubmit">
      <van-field
        v-model="user.phone"
        name="手机号"
        label="手机号"
        placeholder="输入手机号"
        :rules="[{ required: true, message: '请填写手机号' }]"
      />
      <van-field
        v-model="user.nickname"
        name="昵称"
        label="昵称"
        placeholder="昵称"
        :rules="[{ required: true, message: '请填写昵称' }]"
      />
      <van-field
        v-model="user.password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" native-type="submit">注册</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import axios from "axios";
import { reqRegister } from "../../utils/http.js";
import { Toast } from "vant";
export default {
  methods: {
    onSubmit() {
      reqRegister(this.user).then(res => {
        if (res.data.code == 200) {
          Toast(res.data.msg);
          this.$router.push("/login");
        }
      });
    }
  },
  data() {
    return {
      user: {
        phone: "",
        nickname: "",
        password: ""
      }
    };
  }
};
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';

.btn {
  background: $primary;
  padding: 0.1rem 0.5rem;
}
</style>


